.sideBar {
    grid-area: n;
    background-color: white;
    padding: 15px;
    margin: 5px;
}

.item {
    color: black;
    text-decoration: none;
}

.active {
    color: black;
    font-weight: bold;
    text-decoration: none;
}

.cabinet_btn {
    display: inline-block;
    background-color: #fff;
    border: 1px solid #000;
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: 0;
    font-size: 15px;
    font-weight: 500;
    color: #000;
    transition: .2s;
    padding: 11px 15px;
    margin-top: 10px;
    text-transform: uppercase;
    line-height: 1;
    border-radius: 5px;
}
.cabinet_btn:hover {
    background-color: #000;
    color: #fff;
}
