div {
    display: block;
}

.offer .tabs {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    font-family: Montserrat, sans-serif;
    margin-bottom: 40px;
}

.offer .tabs > div:first-child {
    background-color: #C9F3E2;
    background-image: url(https://cryptomannn.com/images/mentorship/offer.png);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
}

.offer .tabs > div {
    width: 100%;
    padding: 50px 50px 90px;
    border: 2px solid gray;
    border-radius: 20px;
}

.offerLeft {
    max-width: 600px;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.offerLeft .subtitle {
    text-transform: none;
    margin-bottom: 24px;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.offerLeft p {
    margin-bottom: 20px;
}

@media only screen and (max-width: 700px) {
    .subtitle, .program .number, .program .name {
        font-size: 28px;
    }
}

.offerLeft > a {
    margin-left: auto;
    margin-bottom: 10px;
}

.btn {
    display: inline-block;
    background-color: #000;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: 0;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    transition: .2s;
    padding: 11px 23px;
    text-transform: uppercase;
    line-height: 1;
    border-radius: 20px;
}
.btn:hover {
    background-color: white;
}
.offer .tabsControls {
    flex-grow: 1;
    align-items: flex-end;
    justify-content: center;
}
.tabs .tabsControls {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.offer .tabs .tabsControls li:not(:last-child) {
    margin-right: 24px;
}
.offer .tabs .tabsControls li {
    width: calc((100% - 48px) / 3);
    align-items: flex-end;
    border: none;
}
.tabs .tabsControls li:not(:last-child) {
    border-right: none;
}
.tabs .tabsControls li {
    min-height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 20%;
    border: 1px solid #E2E2E2;
}

@media only screen and (max-width: 1220px){
    .offer .tabsControls a {
        font-size: 16px;
    }
}

.tabsControls {
    margin-top: 70px;
}

.buyBtn {
    text-transform: uppercase;
    background: #fff;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    padding: 15px 10px;
    font-size: 14px;
    font-weight: 500;
    display: block;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
    border-radius: 10px;
    border: 1px solid black;
}
.tabsControls a:hover {
    background-color: whitesmoke;
    color: inherit;
}
.tabsControls a:active {
    color: #fff;
    background: #000;
}
.offerRight {
    background: linear-gradient(210deg, #fff, #f5f5f5, #f5f5f5, #f5f5f5, #C9F3E2, #f5f5f5, #f5f5f5, #F5F5F5, #FFFFFF);
    background-repeat: no-repeat;
    background-size: 300% 300%;
    animation: gradient 7s ease infinite;
    border: 2px solid gray;
    border-radius: 20px;
    padding: 20px;
}

.tabsContentRight {
    max-width: 522px;
    margin-right: auto;
}
.offer .tabs .tabsContent .contentItem.active {
    padding: 0;
    border: none;
}
.tabsContentRight p {
    border-top: 1.5px solid #E2E2E2;
    padding-top: 15px;
    margin-bottom: 30px;
    position: relative;
}
.tabsContentRight p .subtitle {
    padding-right: 20%;
}
.tabsContentRight s {
    position: absolute;
    top: calc(100% + 16px);
    left: 0;
}
.tabsContentRight .btn {
    min-width: 280px;
    background: #C9F3E2;
    color: #000;
    border: 1px solid #000;
    margin: auto;
    display: block;
    text-align: center;
    width: fit-content;
}
.subtitle {
    font-size: 36px;
    font-weight: 600;
    text-transform: uppercase;
}
nav {
    list-style-type: none;
}