.intro .content {
    margin-top: 30px;
    border: 2px solid gray;
    border-radius: 20px;
}
.content {
    max-width: 1360px;
    padding: 40px 40px;
    margin: 0 auto;
    display: block;
}
.innerBlock  {
    max-width: 1140px;
    margin: 0 auto;
    display: block;
}
.innerBlock video {
    width: 100%;
    margin-bottom: 30px;
    border-radius: 20px;
}
.introText {
    display: flex;
    flex-wrap: wrap;
}
.introText p:first-child {
    margin-right: 64px;
}
.introText p {
    width: calc(50% - 32px);
    margin-bottom: 16px;

}