.features {
    background: url(https://cryptomannn.com/images/mentorship/features-shape.svg) no-repeat top left;
    overflow: hidden;
}

div {
    display: block;
}

li {
    display: list-item;
    text-align: -webkit-match-parent;
}

.content {
    max-width: 1360px;
    padding: 40px 40px;
    display: block;
    border-radius: 20px;
    border: 2px solid gray;
    margin-top: 30px;
}

.features .title {
    margin-bottom: 64px;
}

.title {
    font-size: 60px;
    font-weight: 600;
    text-transform: uppercase;
}

.featuresContent {
    display: flex;
    flex-wrap: wrap;
}

.featuresContent .subtitle {
    margin-bottom: 32px;
}

.featuresContent p {
    font-size: 24px;
    max-width: 400px;
}

.featuresContent > div:last-child {
    width: calc(57% - 40px);
}

.featuresContent li {
    margin-bottom: 24px;
}

.subtitle {
    font-size: 36px;
    font-weight: 600;
    text-transform: uppercase;
}

.featuresContent > div:first-child {
    width: 43%;
    margin-right: 40px;
}
.links {
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-right: 118px;
}
.links a {
    border: 2px solid black;
    border-radius: 20px;
    padding: 10px;
}

.linkArrow {
    border-color: #000;
    color: #000;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
    text-decoration: none;
}

.linkArrow:hover {
    background-color: #000;
    color: #fff;
    cursor: pointer ;
}

ul {
    list-style-type: none;
}