div {
    display: block;
}

h3 {
    display: block;
    font-size: 1.17em;
    margin-block-start: 1em;
    margin-block-end: 1em;
    font-weight: bold;
}

footer {
    display: block;
    width: 100%;
}

.footer {
    background-color: #000;
    color: #fff;
    padding-top: min(50px, 7vw);
    padding-bottom: min(60px, 6vw);
}

.footerCont {
    margin: 0 auto;
    padding: 0 40px;
    display: -ms-grid;
    display: grid;
    grid-template-areas:
        "t s m u e"
        "x s m u e"
        "lang l1 l2 1 b";
    row-gap: min(60px, 6vw);
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}


.footerTitle {
    text-transform: uppercase;
    color: #fff;
    font-size: 1.5em;
    line-height: 1.14;
    max-width: 240px;
    font-weight: 700;
    grid-area: t;
}

.footerBlock {
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    align-items: flex-start;
}

.footer .footerCont > div:first-child {
    grid-area: m;
    align-items: flex-start;
}

.footer .footerCont > div:nth-child(2) {
    grid-area: s;
    align-items: flex-start;
}

.footer .footerCont > div:last-child {
    grid-area: u;
    align-items: flex-start;
}


.footerListTitles {
    color: #fff;
    margin-bottom: 30px;
    margin-left: 40px;
    font-weight: 700;
    font-size: 1.5em;
    text-transform: uppercase;
}

.footerLists {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    row-gap: 25px;
    list-style-type: none;
    float: left;
}

.footerListItem {
    text-transform: uppercase;
    font-weight: 400;
}

.footerListLink {
    text-decoration: none;
    color: #fff;
    position: relative;
    cursor: pointer;
    display: inline-block;
}
