div {
    display: block;
}
.cabinet {
    background: url(https://cryptomannn.com/images/mentorship/audience-bg.svg) no-repeat top right;
    background-size: auto 100%;
    border-radius: 20px;
    border: 2px solid gray;
    margin-top: 30px;
    display: block;
    max-width: 1260px;
    font-family: Poppins-Regular,sans-serif;
}

.cabinetCont {
    width: 100%;
    min-height: 70vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;
}
.cabinetForm {

}
.cabTitle {
    font-family: Poppins-Bold sans-serif;
    font-size: 24px;
    color: #333;
    line-height: 1.2;
    text-align: center;
    width: 100%;
    display: block;
    padding-bottom: 34px;
}
.loginInput {
    position: relative;
    width: 100%;
    z-index: 1;
    margin-bottom: 10px;
}
input {
    outline: none;
    border: none;
}
.input {
    font-family: Poppins-Medium sans-serif;
    font-size: 15px;
    line-height: 1.5;
    color: #666;
    display: block;
    width: 80%;
    background: #e6e6e6;
    height: 50px;
    border-radius: 25px;
    padding: 0 30px 0 28px;
}
.symbolInput {
    font-size: 15px;
    display: flex;
    align-items: center;
    position: absolute;
    border-radius: 25px;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-left: 35px;
    pointer-events: none;
    color: #666;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s;
}
.loginBtn {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
}
button {
    outline: none!important;
    border: none;
}
.loginBtn button {
    font-family: Montserrat-Bold sans-serif;
    font-size: 15px;
    line-height: 1.5;
    color: #fff;
    text-transform: uppercase;
    width: 100%;
    height: 50px;
    border-radius: 25px;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 25px;
    transition: all .4s;
}
.loginBtn button:hover {
    background-color: firebrick;
    cursor: pointer;
}
.forgotText {
    padding-top: 12px;
    text-align: center;
}
.txt1 {
    font-size: 13px;
    line-height: 1.5;
    color: #999;
}
.txt2 {
    font-size: 13px;
    line-height: 1.5;
    color: #666;
}
.createText {
    padding-top: 100px;
    text-align: center;
}
.txt3 {
    font-size: 18px;
    line-height: 1.5;
    color: #666;
}
