.education {
    padding-top: 0px;
    width: 100%;
}

.btn_transperrent {
    text-decoration: none;
    border-color: #e9e9e9;
    color: black;
    background-color: transparent;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease
}

.education_card {
    margin: 20px;
    padding-bottom: 40px;
    border: 2px solid gray;
    border-radius: 20px;
}

.education_card_header {
    -ms-grid-column: 1;
    grid-column: 1;
    -ms-grid-row: 1;
    grid-row: 1;
    z-index: 3;
    padding-left: 10px;
}

.education_card_title {
    text-transform: uppercase;
    color: black;
    font-weight: 700;
    font-size: min(3.125rem, 6vw);
    z-index: 3
}

.education_card_text {
    -ms-grid-column: 2;
    grid-column: 2;
    -ms-grid-row: 1;
    grid-row: 1;
    color: black;
    font-size: 1.375rem;
    max-width: min(600px, 50vw);
    justify-self: flex-end;
    z-index: 3;
    padding-left: 10px;
}

.education_card_actions {
    -ms-grid-column: 2;
    grid-column: 2;
    -ms-grid-row: 2;
    grid-row: 2;
    align-self: flex-end;
    justify-self: flex-end;
    z-index: 3;
    float: right;
    padding-right: 20px;
}

.education_card_guide {
    -ms-grid-row: 1;
    -ms-grid-column: 2;
    -ms-grid-column-span: 3;
    grid-area: c
}

.education_card_mentorship {
    -ms-grid-row: 2;
    -ms-grid-column: 2;
    -ms-grid-column-span: 3;
    grid-area: c1;
    background-color: #e9e9e9
}

.education_card_mentorship:hover .education_card_title {
    color: #fff
}

.education_card_mentorship:hover .education_card_text {
    color: #fff
}

.education_card_mentorship .education_card_text {
    color: #000;
    font-weight: 500;
}

.education_card_mentorship .education_card_title {
    color: #000
}

.btn_secondary {
    color: #000;
    background-color: #e9e9e9;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
    text-decoration: none;
    min-width: 350px;
    min-height: 40px;
    border-radius: 20px;
    border: 2px solid black;
    padding: 10px;
}

.btn_secondary:hover {
    background-color: #000;
    color: #fff;
    cursor: pointer ;
}