.appWrapper {
    margin: 0 auto;
    display: grid;
    width: 1260px;
    grid-template-areas:
  "h h"
  "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}
.appWrapper_content {
    font-family: Montserrat, sans-serif;
    grid-area: c;
    background-color: white;
    padding: 10px;
}