.banner_content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-grow: 1;
    padding-top: 0;
    padding-bottom: 0;
    max-width: 1200px;
}
.banner_content video {
    width: 300px;
    position: relative;
    border-radius: 20px;
}
.banner_text {
    width: 50%;
    padding: 72px 25px 72px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 600px;
}
.banner_text p:nth-child(2) {
    margin-bottom: 40px;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.subLink {
    margin-left: auto;
    text-transform: uppercase;
    font-size: 24px;
    transition: .2s;
    overflow: hidden;
    max-width: 100%;
    padding: 7px 0;
    position: relative;
    height: 30px;
    width: 200px;
    line-height: 1;
}
.subButton {
    border: 2px solid whitesmoke;
    border-radius: 10px;
    color: #000;
    background-color: #e9e9e9;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
    text-decoration: none
}
.subButton:hover {
    background-color: #000;
    color: #fff;
    cursor: pointer ;
}