.content {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    max-width: 1360px;
    border: 2px solid gray;
    border-radius: 20px;
    padding: 30px;
}
.video video {
    width: 360px;
    position: relative;
    border-radius: 20px;
}
.homePageText {
    padding-left: 30px;
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 600px;
    font-size: 20px;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}