.audience {
    background: url(https://cryptomannn.com/images/mentorship/audience-bg.svg) no-repeat top right;
    background-size: auto 100%;
    border-radius: 20px;
    border: 2px solid gray;
    margin-top: 30px;
}
.audience .links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-right: 118px;
}
.linkArrow {
    text-transform: uppercase;
    position: relative;
    padding-left: 65px;
    display: inline-block;
    transition: .2s;
}
a, a:hover {
    text-decoration: inherit;
    color: inherit;
}
.linkArrow:before {
    content: url(https://cryptomannn.com/images/icons/arrow-link.svg);
    position: absolute;
    top: 0;
    left: 0;
}
.content {
    max-width: 1360px;
    padding: 40px 40px;
    margin: 0 auto;
    display: block;
}

.audience .title {
    margin-bottom: 72px;
}
.innerBlock {
    max-width: 1200px;
    margin: 0 auto;
    display: block;
}
.foraudience {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
}
.foraudience > div:not(:last-child) {
    margin-right: 50px;
}
.foraudience > div {
    width: calc((100% - 400px) / 3);
    margin-bottom: 50px;
    background: #C9F3E2;
    padding: 40px 36px;
    border-radius: 10px;
}
.foraudience .subtitle {
    margin-bottom: 28px;
}
.foraudience > div:nth-child(2) ul {
    padding-bottom: 0px;
}
.foraudience > div:nth-child(3) ul {
    padding-bottom: 48px;
}
.foraudience li {
    margin-bottom: 20px;
    font-size: 14px;
}
ul {
    list-style-type: none;
}